import { Component } from '@angular/core';
import { NavController,LoadingController } from 'ionic-angular';
import { RestApiProvider } from '../../providers/rest-api/rest-api';
import { EventDetailPage } from '../event-detail/event-detail';

@Component({
  selector: 'page-schedule',
  templateUrl: 'schedule.html'
})

export class SchedulePage {
  programming: string = "now_playing";
  events: any[] = [];
  schedule: any;
  nowPlaying: any;

  constructor(public navCtrl: NavController, public loadingCtrl:LoadingController, private restApiProvider: RestApiProvider) {
    this.restApiProvider.requestInfo("pages", "include=4638")
      .subscribe(
        schedule => this.schedule = this.getCurrentDay(schedule[0]["content"]["rendered"]));
  }

  ionViewDidEnter() {
    this.restApiProvider.getEvents().subscribe(value => {
      this.events = value;
    })
    this.restApiProvider.getNowPlaying().subscribe(value => {
      this.nowPlaying = value;
    })
  }

  itemTapped(event, item) {
    this.navCtrl.push(EventDetailPage, {
      item: item
    });
  }

  getCurrentDay(schedule:any) {
    var string = schedule + '';
    var scheduleArray = string.split("<h2>");
    var d = new Date();
    var n = d.getDay();

    return("<h2>" + scheduleArray[n+1]);
  }
}



