import { Component } from '@angular/core';
import { NavController,LoadingController } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

import { YoutubeServiceProvider } from '../../providers/youtube-service/youtube-service';

@Component({
  selector: 'page-video',
  templateUrl: 'video.html',
  providers:[YoutubeServiceProvider]
})
export class VideoPage {
  videos: any[] = [];
 
  constructor(public navCtrl: NavController, public youtubeService: YoutubeServiceProvider, public loadingCtrl:LoadingController, public sanitizer: DomSanitizer) {
    this.findVideos();
  }

  findVideos(){
    let loading = this.loadingCtrl.create({});
    loading.present();
 
    this.youtubeService.getVideos().subscribe(
      videos => {
        this.videos=videos.items;
        for(var video of this.videos) {
          this.convertDate(video.snippet.publishedAt);
        }
        loading.dismiss();
      },
      err=>{
        console.log(err);
      }
    );
  }

  sanitizeUrl(url: string) {
    return this.sanitizer.bypassSecurityTrustResourceUrl("https://www.youtube.com/embed/"+url);
  }

  convertDate(date: string) {
    var d = new Date(date);
    var retString: string;
    var monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];

    retString = monthNames[d.getMonth()] + " " + d.getDay() + " " + d.getFullYear();

    return retString;
  }
}



