import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class YoutubeServiceProvider {
  googleToken = "AIzaSyCGfEUsM_DJzwqSjEeqWoVNpgNxPv5yHq8";
  maxResults = 10;
  channelId = 'UCeb_E0tdRLTthfcpOYjNjjQ';
  url = 'https://www.googleapis.com/youtube/v3/search?part=id,snippet';

  constructor(public http: Http) {
  }

  public getVideos(){
    return this.http.get(this.url+'&channelId='+this.channelId+'&type=video&order=date&maxResults='+this.maxResults+'&key='+this.googleToken)
     .map(res => res.json());
  }
}



